<template>
  <div class="profile-user-bar">
    <span class="profile-user-bar__user-name">{{ userName }}</span>
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn icon x-small v-on="on" @click="logout"><v-icon>mdi-logout</v-icon></v-btn>
      </template>
      <span class="tooltip-text tooltip-text-control"></span>
    </v-tooltip>
  </div>
</template>

<script>
// import apiLocalStorages from '@/logics/ApiLocalStorages';

export default {
  name: 'ProfileUser',
  props: {
    // userName: { type: String, default: '' },
  },
  computed: {
    userName() {
      return this.$store.getters['Login/hasAuthorization'] || '';
    },
  },
  methods: {
    logout() {
      // apiLocalStorages.deleteValue('userToken');
      // apiLocalStorages.deleteValue('userName');
      // apiLocalStorages.deleteValue('userPassword');
      this.$store.dispatch('Login/logout');
      // login.logout();
      this.$router.push('/Logout');
    },
  },
};
</script>

<style lang="scss" scoped>
.profile-user-bar {
  display: inline-flex;
  align-items: center;

  &__user-name {
    font-size: 0.9em;
    margin: 0px 5px;
    color: white;
  }
}
</style>
